@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  font-family: "Inter", sans-serif;
  font-size: 16px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
}

.container {
  width: 100%;
  max-width: 1180px;
  margin: auto;
}
@media screen and (max-width: 1200px) {
  .container {
    max-width: 960px;
  }
}
@media screen and (max-width: 992px) {
  .container {
    max-width: 720px;
  }
}
@media screen and (max-width: 768px) {
  .container {
    max-width: none;
    padding: 0 15px;
  }
}

@media screen and (max-width: 992px) {
  .mob-menu-active {
    overflow: hidden;
  }
}

.logo {
  color: #000000;
}
.logo__wrapper {
  display: flex;
  align-items: center;
}
.logo__link {
  text-decoration: none;
  color: inherit;
}
.logo__image {
  width: 41px;
  height: 41px;
  margin-right: 8px;
  object-fit: contain;
}
@media screen and (max-width: 992px) {
  .logo__image {
    width: 28px;
    height: 28px;
    margin-right: 6px;
  }
}
.logo__text {
  font-weight: 600;
  line-height: 136%;
}
@media screen and (max-width: 992px) {
  .logo__text {
    font-size: 10px;
  }
}

.button {
  display: inline-block;
  padding: 9px 32px 7px;
  font-weight: 500;
  line-height: 136%;
  color: #FFFFFF;
  text-decoration: none;
  white-space: nowrap;
  background-color: #000000;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
}
.button--light {
  background-color: #FFFFFF;
  color: #000000;
}

.menu-button {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  width: 24px;
  height: 24px;
  padding: 6px 2px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.menu-button::before, .menu-button::after {
  content: "";
}
.menu-button::before, .menu-button::after, .menu-button__inner {
  display: block;
  width: 20px;
  height: 2px;
  border-radius: 5px;
  background-color: #292D32;
}

.header {
  padding: 10px 0;
  background-color: #FFFFFF;
}
@media screen and (max-width: 992px) {
  .header {
    padding: 6px 0;
  }
}
.header__wrapper {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 992px) {
  .header__wrapper {
    justify-content: space-between;
  }
}
.header__logo {
  margin-right: 94px;
}
.header__nav-overlay {
  flex-grow: 1;
}
@media screen and (max-width: 992px) {
  .header__nav-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s;
  }
  .header__nav-overlay--active {
    opacity: 1;
    pointer-events: auto;
  }
  .header__nav-overlay--active .header__nav-wrap {
    right: 0;
  }
}
.header__nav-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 992px) {
  .header__nav-wrap {
    position: absolute;
    top: 0;
    right: -110%;
    width: 81.9%;
    flex-direction: column;
    justify-content: start;
    max-width: 500px;
    height: 100%;
    padding: 67px 20px;
    background-color: #FFFFFF;
    overflow-y: auto;
    transition: right 0.3s;
  }
}
.header__nav-close {
  display: none;
  position: absolute;
  top: 8px;
  right: 20px;
  width: 24px;
  height: 24px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media screen and (max-width: 992px) {
  .header__nav-close {
    display: block;
  }
}
.header__nav-close::before, .header__nav-close::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 2px;
  border-radius: 5px;
  background-color: #292D32;
}
.header__nav-close::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.header__nav-close::after {
  transform: translate(-50%, -50%) rotate(135deg);
}
.header__nav {
  margin-right: 40px;
}
@media screen and (max-width: 992px) {
  .header__nav {
    margin: 0 0 51px 0;
  }
}
.header__nav-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media screen and (max-width: 992px) {
  .header__nav-list {
    flex-direction: column;
  }
}
.header__nav-item {
  margin-right: 26px;
  padding: 5px 0;
  font-weight: 600;
  line-height: 136%;
}
.header__nav-item:last-child {
  margin-right: 0;
}
@media screen and (max-width: 992px) {
  .header__nav-item {
    margin: 0 0 40px 0;
    padding: 0;
  }
  .header__nav-item:last-child {
    margin-bottom: 0;
  }
}
.header__nav-link {
  text-decoration: none;
  color: #737373;
  transition: 0.3s ease;
}
.header__nav-link--active, .header__nav-link:hover, .header__nav-link:active {
  color: #000000;
}
.header__menu-button {
  display: none;
}
@media screen and (max-width: 992px) {
  .header__menu-button {
    display: flex;
  }
}